



import { combineReducers } from 'redux-immutable';

import { reducer as homeReducer } from '../pages/home/store';// store
import { reducer as LoginReducer } from '../pages/login/store/';// store
import { reducer as projectManReducer } from "../pages/projectMan//store";// store
import { reducer as userMapPageReducer } from "../pages/userManPage/store";// store
import { reducer as statisticePageReducer } from '../pages/statisticePage/store'; // store
import { reducer as statPersonWorkReducer } from "../pages/statisticePage/components/personWorkStat/store";//  

const reducer = combineReducers({
	home:homeReducer,
	login:LoginReducer,
	project:projectManReducer,
	userPage:userMapPageReducer,
	statPage:statisticePageReducer,
	statPersonWorkCom:statPersonWorkReducer,
});


export default reducer;











